/**
*
* Global
*
**/
html, body {
  height: 100%;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

::selection {
  background: blue;
  color: #FFFFFF;
}

p {
  line-height: 1.6;
}

.display-type-1 {
  display: flex;
  flex-direction: row;
}

.header {
  margin: 20px;
  padding: 12px;
}
.header .logo {
  width: 50%;
  padding-left: 15px;
}

.bg-color-darkcyan {
  background-color: #008b8b;
}

.bg-color-gainsboro {
  background-color: #dcdcdc;
}

.bg-color-honeydew {
  background-color: #f0fff0;
}

.bg-color-darkseagreen {
  background-color: #8fbc8f;
}

.bg-color-plum {
  background-color: #673147;
}

.bg-color-black {
  background-color: #000000;
}

.bg-color-white {
  background-color: #FFFFFF;
}

.bg-color-antiquewhite {
  background-color: #faebd7;
}

.font-color-darkcyan {
  color: #008b8b;
}

.font-color-gainsboro {
  color: #dcdcdc;
}

.font-color-honeydew {
  color: #f0fff0;
}

.font-color-darkseagreen {
  color: #8fbc8f;
}

.font-color-plum {
  color: #673147;
}

.font-color-black {
  color: #000000;
}

.font-color-white {
  color: #FFFFFF;
}

.font-color-antiquewhite {
  color: #faebd7;
}

.menus {
  width: 80%;
}
.menus.display-type-2 {
  display: block;
  margin-left: 30px;
  padding: 10px;
}
.menus .menu-list {
  margin: 0px;
  padding: 0px;
  list-style-type: none;
}
.menus .menu-list .items {
  width: 150px;
  height: 50px;
  padding-top: 17px;
  margin-right: 10px;
  text-align: center;
}
.menus .menu-list .items:hover {
  text-decoration: underline;
  background-color: red;
}

.btn {
  width: 75px;
  height: 30px;
  padding: 6px;
  margin-left: 22px;
  cursor: pointer;
}

.btn-primary {
  color: #FFFFFF;
  background-color: #0074d9;
  border-radius: 3px;
}

.btn-warning {
  color: #FFFFFF;
  background-color: #ff4136;
  border-radius: 3px;
}

.btn-success {
  color: #FFFFFF;
  background-color: #5cb85c;
  border-radius: 3px;
}

.link {
  text-decoration: none;
}

.content .left-col {
  border: 2px solid red;
  width: 20%;
}
.content .left-content {
  display: block;
  margin-left: 30px;
  padding: 10px;
}

.content .right-col {
  border: 2px solid red;
  width: 80%;
}
.content .right-col .title {
  margin-left: 15px;
  padding: 10px;
}
.content .right-col .description {
  margin-left: 15px;
  padding: 10px;
}
.content .right-col .content-top {
  margin-top: -units(25, px);
}
.content .right-col .row .col {
  width: 25%;
  padding: 20px;
  margin: 3px;
}

.nav {
  background-color: #333;
}
.nav a {
  float: left;
  color: #f2f2f2;
  background-color: #333;
  padding: 14px 16px;
  text-decoration: none;
  font-size: 17px;
}
.nav a:hover {
  background-color: #ddd;
  color: black;
}
.nav .nav-right {
  float: right;
}
.nav a.active {
  background-color: #04AA6D;
  color: white;
}